import axios from 'axios';
import { Module } from 'vuex';
import {
  RootState, SubscriptionState,
} from './types';

const subscription: Module<SubscriptionState, RootState> = {
  namespaced: true,
  state: {
    topicList: [],
  },
  mutations: {
    SAVE_TOPIC_LIST(state, list) {
      state.topicList = list;
    },
  },
  actions: {
    getTopic({ commit }) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/GetTopic',
            {
              serid: '4010',
              security: 'A3B0B958-DC13-4827-B663-3E4C130F28D6',
            })
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_TOPIC_LIST', data.Data);
            resolve(data.Data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
  },
  getters: {

  },
};

export default subscription;
